.wrapper {
    display: flex;
    align-items: center;
}

.icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    background-color: var(--accent-bg-color);
    color: var(--second-text-color);

    margin-right: 5px;
    padding: 15px;
}

@media screen and (min-width: 768px) {
    .icon {
        margin-right: 20px;
    }
}

@media screen and (min-width: 1440px) {
    .icon {
        margin-right: 30px;
    }
}

.number {
    font-weight: 700;
    margin-right: 5px;
}

@media screen and (min-width: 768px) {
    .number {
        margin-right: 20px;
    }
}

@media screen and (min-width: 1440px) {
    .number {
        margin-right: 30px;
    }
}

.button {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    width: 40px;
    height: 40px;
    padding: 10px;

    border: none;
    border-radius: 5px;
    background-color: #2b2b2b;
    color: var(--second-text-color);

    cursor: pointer;
    transition: background-color var(--main-hover-animation);
}

.button:hover,
.button:focus {
    background-color: var(--accent-bg-color);
}

.item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
}

.item:last-child {
    margin-bottom: 0;
}